*{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin: 0;
}
html, body, #root{
	padding: 0;
	height: 100%;
	min-height: -webkit-fit-content;
	min-height: -moz-fit-content;
	min-height: fit-content;
	width: 100%;
	font-family: 'Open Sans', sans-serif;
}

/*  Pagina Principal  */
.principal{
	height: 100%;
	min-height: -webkit-fit-content;
	min-height: -moz-fit-content;
	min-height: fit-content;
	position: absolute;
	width: 100%;
	background-color: #DFE2DB;
}
	.menu-lateral{
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
		min-height: 100%;
		width: 330px;
		position: absolute; /*Cambiar en caso de que la pantalla se pequeÃ±a de altura*/
		top: 0;
		left: 0;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: start;
		    justify-content: flex-start;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-flow: column nowrap;
		    flex-flow: column nowrap;
		background-color: #222;
		color: #ccc;
	}
		.menu-seccion{
			margin-bottom: 2rem;
			width: 90%;
			min-height: -webkit-fit-content;
			min-height: -moz-fit-content;
			min-height: fit-content;
			height: 100px; /*Por quitar*/
		}
		.menu-presentacion{
			margin-top: 3rem;
			display: -ms-flexbox;
			display: flex;
			-ms-flex-flow: column nowrap;
			    flex-flow: column nowrap;
		}
			.titulo-presentacion{
				margin: 0 0 1rem;
				border-bottom: 3px solid rgba(150, 10, 10, 1);
				color: #ddd;
				width: 75%;
				padding-right: .5rem;
				letter-spacing: 2px;
			}
		.menu-redes{
			width: 100%;
			height: 70px;
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: center;
			    align-items: center;
			-ms-flex-pack: center;
			    justify-content: center;
			background-color: rgba(100, 10, 10, 1);
		}
		.contenedor-redes-sociales{
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: center;
			    align-items: center;
			-ms-flex-pack: distribute;
			    justify-content: space-around;
			height: 100%;
			width: 80%;
		}
			.red-social{
				width: -webkit-fit-content;
				width: -moz-fit-content;
				width: fit-content;
				height: 100%;
				padding: .6rem .8rem;
				-webkit-transition: all .5s ease;
				-o-transition: all .5s ease;
				transition: all .5s ease;
			}
				.red-social>div.link{
					width: -webkit-fit-content;
					width: -moz-fit-content;
					width: fit-content;
					height: -webkit-fit-content;
					height: -moz-fit-content;
					height: fit-content;
					margin: auto;
					display: block;
					color: #ccc;
					-webkit-transition: all .5s ease;
					-o-transition: all .5s ease;
					transition: all .5s ease;
				}
			.red-social:hover{
				background-color: #ccc;
			}
				.red-social:hover>div.link{
					color: rgba(100, 10, 10, 1);
					-webkit-transform: scale(1.1);
					    -ms-transform: scale(1.1);
					        transform: scale(1.1);
				}
		.menu-links{
			display: -ms-flexbox;
			display: flex;
			-ms-flex-flow: column nowrap;
			    flex-flow: column nowrap;
			-ms-flex-align: center;
			    align-items: center;
			-ms-flex-pack: distribute;
			    justify-content: space-around;
		}
			.link-web{
				height: 45px;
				margin: .4rem 0;
				width: 50%;
				border-radius: 20px;
				background-color: rgba(0, 0, 0, 0.4);
				border: 3px solid rgba(100, 10, 10, 1);
				-webkit-transition: all .5s ease;
				-o-transition: all .5s ease;
				transition: all .5s ease;
				overflow: hidden;
			}
				.link-direccion{
					font-size: 16pt;
					display: -ms-flexbox;
					display: flex;
					color: #ccc;
					-ms-flex-align: center;
					    align-items: center;
					-ms-flex-pack: center;
					    justify-content: center;
					font-weight: 700;
					width: 100%;
					height: 100%;
					text-decoration: none;
				}
			.link-web:hover{
				background-color:rgba(100, 10, 10, 1);
				border-color: #ccc;
				-webkit-transform: scale(1.05);
				    -ms-transform: scale(1.05);
				        transform: scale(1.05);
			}
				.link-web:hover>.link-direccion{
					color: #ccc;
					background-color: rgba(100, 10, 10, 1);
				}
			
			.link-web-activo{
				color: black;
				background-color: #ccc;
			}

	.contenidos{
		background-color: #DFE2DB;
		position: relative;
		overflow: hidden;
		left: 330px;
		top: 0;
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
		min-height: 100%;
		width: calc(100% - 330px);
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: center;
		    justify-content: center;
	}
	
/* About  */
.contenido-about{
	width: 90%;
	margin: 2rem 0;
	min-height: -webkit-fit-content;
	min-height: -moz-fit-content;
	min-height: fit-content;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}
	.nombre-about{
		text-align: center;
		margin-bottom: 2rem;
		position: absolute;
		width: 100%;
		height: 60px;
		background-color: #222;
		padding: .5rem;
	}
		.nombre-about>h1{
			margin: 0;
			color: #ddd;
		}
	.presentacion-about{
		margin-top: 7rem;
		width: 100%;
		height: 80%;
		min-height: -webkit-fit-content;
		min-height: -moz-fit-content;
		min-height: fit-content;
		display: -ms-flexbox;
		display: flex;
		background-color: #222;
		/*border: 3px solid rgba(100, 10, 10, 1);*/
		color:#ccc;
	}
		.categoria-presentacion{
			width: 50%;
			height: 100%;
			min-height: -webkit-fit-content;
			min-height: -moz-fit-content;
			min-height: fit-content;
			display: -ms-flexbox;
			display: flex;
			-ms-flex-flow: column nowrap;
			    flex-flow: column nowrap;
			-ms-flex-pack: distribute;
			    justify-content: space-around;
			position: relative;
			padding: .5rem;
			padding-top: 20px;
		}
		.categoria-habilidades{
			border-left: 3px solid rgba(100, 10, 10, 1);
		}
		.categoria-personal{
			border-right: 3px solid rgba(100, 10, 10, 1);
		}	
			.presentacion-titulo{
				height: 30px;
				width: 200px;
				position: absolute;
				top:-15px;
				left: calc(50% - 100px);
				text-align: center;
				color: #ccc;
				background-color: rgba(100, 10, 10, 1);
				display: -ms-flexbox;
				display: flex;
				-ms-flex-align: center;
				    align-items: center;
				-ms-flex-pack: center;
				    justify-content: center;
				padding-bottom: 2px;
			}
			.presentacion-descripcion>p{
				width: 90%;
				margin: auto;
				text-align: center;
			}
			.presentacion-caracteristicas{
				display: -ms-flexbox;
				display: flex;
				-ms-flex-flow: column nowrap;
				    flex-flow: column nowrap;
				-ms-flex-align: stretch;
				    align-items: stretch;
			}
				.info-caracteristicas{
					background-color: rgba(0, 0, 0, 0.7);
					width: 90%;
					margin: auto;
					padding: .4rem .4rem;
				}
					.info-caracteristicas>span{
						color: rgba(150, 10, 10, 1);
					}
			.presentacion-hobbies{
				display: -ms-flexbox;
				display: flex;
				-ms-flex-pack: center;
				    justify-content: center;
				-ms-flex-line-pack: center;
				    align-content: center;
			}
				.hobbie-seccion{
					border-radius: 50%;
					margin: 0 .5rem;
					background-color: red;
					width: 80px;
					height: 80px;
				}
			/*.presentacion-habilidad{}*/
			.categoria-habilidades h4{
				border-bottom: 2px solid rgba(100, 10, 10, 1);
			}
			.categoria-habilidades p{
				margin-left: .5rem;
				font-size: 11pt;
			}
			.apartado-categoria{
				margin: 10px 0;
			}

/*Proyectos*/
.contenido-proyectos{
	width: 90%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
}
	.seleccionar-proyectos{
		margin-top: 2rem;
		background-color: #222;
		width: 100%;
		height: 60px;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		position: absolute;
	}
		.categoria-proyectos{
			height: 100%;
			width: 100px;
			background-color: rgba(100, 10, 10, 1);
			font-size: 15pt;
			font-weight: 500;
			color: white;
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: center;
			    align-items: center;
			-ms-flex-pack: center;
			    justify-content: center;
			-webkit-transition: all .5s ease;
			-o-transition: all .5s ease;
			transition: all .5s ease;
			position: relative;
			cursor: pointer;
		}
		.categoria-proyectos:hover{
			background-color: #ccc;
			color: rgba(100, 10, 10, 1);
		}
		.categoria-proyectos:after{
			content: "";
			display: block;
			width: 10px;
			height: 2px;
			background-color: rgba(100, 10, 10, 1);
			position: absolute;
			bottom: 13px;
			left: calc(50% - 5px);
			z-index: 3;
			-webkit-transition: all .3s ease;
			-o-transition: all .3s ease;
			transition: all .3s ease;
			opacity: 0;
		}
		.categoria-proyectos:hover:after{
			width: 40px;
			left: calc(50% - 20px);
			opacity: 1;
		}

	.contenedor-proyectos{
		margin-top: 9rem;
		width: 100%;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-flow: column nowrap;
		    flex-flow: column nowrap;
	}
		.info-proyecto{
			height: 200px;
			margin-bottom: 2rem;
			background-color: #333;
			border-radius: 5px;
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: stretch;
			    align-items: stretch;
			-ms-flex-pack: distribute;
			    justify-content: space-around;
			-webkit-transition: all 1s ease-out;
			-o-transition: all 1s ease-out;
			transition: all 1s ease-out;
			-webkit-box-shadow: 0px 0px 5px 1px black;
			        box-shadow: 0px 0px 5px 1px black;
		}
			.seccion-proyecto{
				width: 30%;
			}
				.seccion-proyecto>.link-foto{
					display: block;
					height: 100%;
					width: 100%;
				}
					.link-foto>img{
						width: 100%;
						height: 100%;
					}
			.seccion-proyecto-2{
				width: 60%;
				overflow: hidden;
				position: relative;
			}
				.contenedor-efecto-info{
					height: 200%;
					position: relative;
					top: 0;
					display: -ms-flexbox;
					display: flex;
					-ms-flex-flow: column wrap;
					    flex-flow: column wrap;
					-webkit-transition: all .5s ease-in-out;
					-o-transition: all .5s ease-in-out;
					transition: all .5s ease-in-out;
				}
				.info-principal{
					width: 100%;
					padding: 1rem;
					height: 50%;
					position: relative;
					background-color: #ccc;
				}
					.info-principal>h3{
						color: rgba(100, 10, 10, 1);
						font-size: 17pt;
						width: 100%;
						height: 29px;
						padding-right: .7rem;
						border-bottom: 2px solid #222;
						margin-bottom: 1rem;
					}
					.info-principal>.resumen{
						font-style: italic;
						margin-bottom: 1.5rem;
					}
					.info-principal span{
						font-weight: bold;
						font-size: 14pt;
					}
					.info-principal>.tecnologias{
						font-size: 11pt;
					}
					.boton-info{
						position: absolute;
						width: 100%;
						height: 30px;
						left: 0;
						cursor: pointer;
					}
					.boton-info:hover .flecha-boton{
						border-color: white;
					}
					.boton-mas-info{
						bottom: 0;
					}
					.boton-menos-info{
						top: 0;
					}
						.contenedor-flechas{
							width: 6%;
							height: 100%;
							position: relative;
							left: calc(50% - 3%);
						}
						.boton-mas-info>.contenedor-flechas{
							top:-10px;
						}
						.boton-menos-info>.contenedor-flechas{
							bottom: -5px;
						}
							.flecha-boton{
								width: 50%;
								height: 50%;
								margin: auto;
								margin-bottom: -.3rem;
								border-right: 3px solid black;
								border-bottom: 3px solid black;
								-webkit-transition: all .3s ease;
								-o-transition: all .3s ease;
								transition: all .3s ease;
							}
							.boton-mas-info .flecha-boton{
								-webkit-transform: rotate(45deg);
								    -ms-transform: rotate(45deg);
								        transform: rotate(45deg);
							}
							.boton-menos-info .flecha-boton{
								-webkit-transform: rotate(-135deg);
								    -ms-transform: rotate(-135deg);
								        transform: rotate(-135deg);
							}

				.info-resumen{
					width: 100%;
					height: 50%;
					position: relative;
					background-color: rgba(100, 10, 10,1);
					display: -ms-flexbox;
					display: flex;
					-ms-flex-align: center;
					    align-items: center;
					-ms-flex-pack: center;
					    justify-content: center;
					padding: 0 1rem;
					color: #ccc;
					font-weight: 700;
				}


.efecto-aparicion{
	opacity: 0;
	-webkit-transform: translateX(150px);
	    -ms-transform: translateX(150px);
	        transform: translateX(150px);
}
.efecto-movimiento{
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
}
.efecto-boton-activo{
	background-color: #ccc;
	color: rgba(100, 10, 10, 1);
}
.efecto-boton-activo:after{
	width: 40px;
	left: calc(50% - 20px);
	opacity: 1;
}


